.App {
  text-align: center;
  font-size: 25px;
  color: white;
}

body {
  background-color: #353535;
}

.header {
  background-color: #284b63;
  padding: 10px;
  margin-bottom: 10px;
}
.container {
  max-width: 800px;
  margin: 0 auto;
  padding: 0 30px ;
}

h1 {
  color: #d9d9d9;
  font-size: 32px;
  padding: 0;
  margin: 0;
}

.pattern-button {
  background-color: #284b63;
  color: #d9d9d9;
  font-size: 1rem;
  margin: 0;
  padding: 1rem;
  width: 60%;
}

.setButt {
  background-color: #d9d9d9;
  color: #353535;
  height: 35px;
  width: 35px;
}

.setText {
  color: #353535;
  width: 40px;
  height: 28px;
  text-align: center;
  margin: 0 2px;
  padding: 0;
}

.specialButton {
  background-color: #d9d9d9;
  margin-top: 20px;
  height: 35px;
}

.ReactModalPortal > div {
  opacity: 0;
}

.ReactModalPortal .ReactModal__Overlay {
  align-items: center;
  display: flex;
  justify-content: center;
  transition: opacity 200ms ease-in-out;
}

.ReactModalPortal .ReactModal__Overlay--after-open {
  opacity: 1;
}

.ReactModalPortal .ReactModal__Overlay--before-close {
  opacity: 0;
}

.modal {
  background: #353535;
  color: #FFFFFF;
  max-width: 300px;
  outline: none;
  padding: 10px 40px;
  text-align: center;
}

.modal__title {
  margin: 0 0 12px 0;
}

.modal__desc {
  margin: 16px 0 2px 0;
  padding-bottom: 3px;
}